@import 'assets/styles/_dimensions.scss';

.modern-header {
  border-bottom: 1px solid rgba(54, 202, 186, 0.3);
  background-color: rgba(28, 24, 23, 0.95);
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
  padding: 0.5rem 0;
  transition: all 0.3s ease;
  position: relative;
  
  &::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background: linear-gradient(90deg, transparent, #36caba, transparent);
    opacity: 0.7;
  }
  
  .header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    @media (max-width: 767px) {
      flex-direction: column;
      justify-content: center;
      padding: 1rem 0;
    }
  }
  
  .logo-container {
    position: relative;
    transition: all 0.3s ease;
    margin-right: 2rem;
    
    @media (max-width: 767px) {
      margin-right: 0;
      margin-bottom: 1rem;
      display: flex;
      justify-content: center;
    }
    
    &:hover {
      transform: scale(1.05);
    }
    
    .logo-image {
      filter: drop-shadow(0 0 8px rgba(54, 202, 186, 0.6));
      object-fit: contain;
    }
  }
  
  .custom-toggler {
    border-color: rgba(54, 202, 186, 0.7);
    
    .navbar-toggler-icon {
      background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%2854, 202, 186, 0.7%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
    }
    
    &:focus {
      box-shadow: 0 0 0 0.25rem rgba(54, 202, 186, 0.25);
    }
  }
  
  .wallet-connect-container {
    display: flex;
    justify-content: flex-end;
    margin-left: auto;
    
    @media (max-width: 767px) {
      justify-content: center;
      margin: 0 auto;
      width: 100%;
    }
  }
  
  .button-group {
    position: relative;
    z-index: 10;

    @media (max-width: 990px) {
      position: relative;
      width: fit-content;
      margin: 0 auto;
    }
    
    .connect-btn {
      background: linear-gradient(45deg, #36caba, #2a9e8e) !important;
      border: none !important;
      padding: 0.6rem 1.5rem !important;
      border-radius: 30px !important;
      color: white !important;
      font-family: 'TheBoldFont', 'Wendy One', sans-serif !important;
      font-size: 1rem !important;
      letter-spacing: 1px !important;
      box-shadow: 0 4px 15px rgba(54, 202, 186, 0.4) !important;
      transition: all 0.3s ease !important;
      display: flex !important;
      align-items: center !important;
      justify-content: center !important;
      position: relative;
      overflow: hidden;
      
      &::before {
        content: '';
        position: absolute;
        top: -2px;
        left: -2px;
        right: -2px;
        bottom: -2px;
        background: linear-gradient(45deg, #36caba, transparent, #36caba);
        z-index: -1;
        border-radius: 30px;
        animation: borderRotate 3s linear infinite;
        opacity: 0;
        transition: opacity 0.3s ease;
      }
      
      &:hover {
        transform: translateY(-2px);
        box-shadow: 0 6px 20px rgba(54, 202, 186, 0.6) !important;
        
        &::before {
          opacity: 1;
        }
      }
      
      .btn-icon {
        margin-right: 8px;
        font-size: 1.1rem;
      }
    }
    
    .network-btn {
      background: linear-gradient(45deg, #ff9800, #f57c00) !important;
      border: none !important;
      padding: 0.6rem 1.5rem !important;
      border-radius: 30px !important;
      color: white !important;
      font-family: 'TheBoldFont', 'Wendy One', sans-serif !important;
      font-size: 1rem !important;
      letter-spacing: 1px !important;
      box-shadow: 0 4px 15px rgba(255, 152, 0, 0.4) !important;
      transition: all 0.3s ease !important;
      display: flex !important;
      align-items: center !important;
      
      &:hover {
        transform: translateY(-2px);
        box-shadow: 0 6px 20px rgba(255, 152, 0, 0.6) !important;
      }
      
      .btn-icon {
        margin-right: 8px;
        font-size: 1.1rem;
      }
    }

    .wallet-connected {
      @media (max-width: 450px) {
        flex-direction: column;
        gap: 0.5rem !important;
      }
      
      .chain-btn {
        background: linear-gradient(45deg, #36caba, #2a9e8e) !important;
        border: none !important;
        padding: 0.6rem 1.2rem !important;
        border-radius: 30px !important;
        color: white !important;
        font-family: 'TheBoldFont', 'Wendy One', sans-serif !important;
        font-size: 0.9rem !important;
        letter-spacing: 1px !important;
        box-shadow: 0 4px 15px rgba(54, 202, 186, 0.4) !important;
        transition: all 0.3s ease !important;
        
        &:hover {
          transform: translateY(-2px);
          box-shadow: 0 6px 20px rgba(54, 202, 186, 0.6) !important;
          
          .chain-icon {
            transform: scale(1.1);
          }
        }
        
        .chain-name {
          font-weight: bold;
        }
      }
      
      .account-btn {
        background: linear-gradient(45deg, #36caba, #2a9e8e) !important;
        border: none !important;
        padding: 0.6rem 1.2rem !important;
        border-radius: 30px !important;
        color: white !important;
        font-family: 'TheBoldFont', 'Wendy One', sans-serif !important;
        font-size: 0.9rem !important;
        letter-spacing: 1px !important;
        box-shadow: 0 4px 15px rgba(54, 202, 186, 0.4) !important;
        transition: all 0.3s ease !important;
        display: flex !important;
        align-items: center !important;
        
        &:hover {
          transform: translateY(-2px);
          box-shadow: 0 6px 20px rgba(54, 202, 186, 0.6) !important;
        }
        
        .account-icon {
          margin-right: 8px;
          font-size: 1.1rem;
        }
        
        .account-text {
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          max-width: 120px;
          
          @media (max-width: 768px) {
            max-width: 80px;
          }
          
          .balance-text {
            opacity: 0.8;
            font-size: 0.85rem;
          }
        }
      }
    }
  }
  
  .main-nav {
    .nav-item {
      font-family: 'TheBoldFont', 'Wendy one', sans-serif;
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: 30px;
      padding: 0.5rem 1rem;
      margin: 0 0.2rem;
      color: rgba(255, 255, 255, 0.8);
      position: relative;
      transition: all 0.3s ease;
      
      &::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 50%;
        width: 0;
        height: 2px;
        background: #36caba;
        transform: translateX(-50%);
        transition: width 0.3s ease;
      }
      
      &:hover {
        color: #36caba;
        transform: translateY(-2px);
        
        &::after {
          width: 70%;
        }
      }
      
      &.active, &.show {
        color: #36caba;
        text-shadow: 0 0 10px rgba(54, 202, 186, 0.5);
        
        &::after {
          width: 90%;
        }
      }
    }
  }
}

@keyframes borderRotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes glow {
  0% {
    box-shadow: 0 0 5px rgba(54, 202, 186, 0.5);
  }
  50% {
    box-shadow: 0 0 20px rgba(54, 202, 186, 0.8);
  }
  100% {
    box-shadow: 0 0 5px rgba(54, 202, 186, 0.5);
  }
}
